<template>
  <body id="paper">
  <el-form :model="loginForm" :rules="rules" class="login-container" label-position="left"
           label-width="0px" v-loading="loading">
    <h3 class="login_title">用户注册</h3>
    <el-form-item prop="username">
      <el-input type="text" v-model="loginForm.username"
                auto-complete="off" placeholder="邮箱"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="loginForm.password"
                auto-complete="off" placeholder="密码"></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="loginForm.repassword"
                auto-complete="off" placeholder="确认密码"></el-input>
    </el-form-item>
    <el-form-item style="width: 100%">
      <el-button type="primary" style="width: 40%;background: #505458;border: none" v-on:click="register">注册</el-button>
    </el-form-item>
  </el-form>
  </body>
</template>
<script>
  const axios = require('axios')
  export default{
    data () {
      // eslint-disable-next-line no-unused-vars
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'))
        } else if (value !== this.ruleForm.pass) {
          callback(new Error('两次输入密码不一致!'))
        } else {
          callback()
        }
      }
      return {
        rules: {
          username: [{ required: true, message: '用户名不能为空', trigger: 'blur' },
            { type: 'email', message: '请输入正确的邮箱地址', trigger: ['blur', 'change'] }],
          password: [{required: true, message: '密码不能为空', trigger: 'blur'}],
          repassword: [{required: true, message: '重复输入密码', trigger: 'blur'}]
        },
        checked: true,
        loginForm: {
          username: '',
          password: '',
          repassword: ''
        },
        loading: false
      }
    },
    methods: {
      check (mail, pass, chpass) {
        let regmail = /^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
        let regpass = /^\w{6,18}$/
        if (!regmail.test(mail)) {
          this.$alert('邮箱格式错误', '提示', {
            confirmButtonText: '确定'
          })
          return false
        }
        if (!regpass.test(pass) || !regpass.test(chpass)) {
          this.$alert('密码格式错误', '提示', {
            confirmButtonText: '确定'
          })
          return false
        }
        if (pass != chpass){
          this.$alert('密码输入不一致', '提示', {
            confirmButtonText: '确定'
          })
          return false
        }
        return true
      },

      register () {
        if (!this.check(this.loginForm.username, this.loginForm.password, this.loginForm.repassword)){
          return
        }
        var _this = this
        axios({
          method: 'post',
          url: '/register',
          data: {'mail': this.loginForm.username, 'password': this.loginForm.password},
          header: {
            // 如果写成contentType会报错
            'Content-Type': 'application/json'
          }
        }).then(resp => {
            if (resp.data.uid !== undefined) {
              this.$alert('注册成功', '提示', {
                confirmButtonText: '确定'
              })
              _this.$router.replace('/Login')
            } else {
              this.$alert('注册失败', '提示', {
                confirmButtonText: '确定'
              })
            }
          })
          .catch(failResponse => {})
      }
    }
  }
</script>
<style>
  body{
    margin: -5px 0px;
  }
  .login-container {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 90px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
  }
  .login_title {
    margin: 0px auto 40px auto;
    text-align: center;
    color: #505458;
  }
  .login_remember {
    margin: 0px 0px 35px 0px;
    text-align: left;
  }
</style>



