<template>
  <div id="register">
    <register></register>
  </div>
</template>

<script>
  import register from '@/components/Register'
    export default {
        name: 'registerApp',
        components: {
          'register': register
        }
    }
</script>

<style scoped>
  #register {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    /*margin-top: 10px;*/
    box-shadow: 0 2px 4px 0 rgba(0,0,0,.05);
  }
</style>



